<template>
	<div class="show-container">
		<div class="show-header">
			<div class="show-zhong">
				<div class="show-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="show-ti">展示面</div>
				<div></div>
			</div>
		</div>
		<div class="show-content">
			<div v-for="item in panelss_list">
				<div v-if="data.user_vip == 2 || data.user_vip == 3">
					<div class="show-between" @click="go_showtu(item.panels_id,item.panels_title)">
						<div class="show-left">{{item.panels_title}}</div>
						<div class="show-jiantou">
							<img src="../assets/btn_enter.png">
						</div>
					</div>
				</div>
				<div v-else-if="data.user_vip == 0 || data.user_vip == 1">
					<div class="show-between" @click="go_showtu(item.panels_id,item.panels_title)">
						<div class="show-left">{{item.panels_title}}</div>
						<div class="show-jiantou">
							<img src="../assets/btn_enter.png">
						</div>
					</div>
				</div>
			</div>
			<!--<div class="show-chongzhi" v-if="data.user_vip == 0 || data.user_vip == 1">请充值中级或高级会员查看更多</div>-->
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				data: [],
				panelss_list: [],
				data: {}
			}
		},
		methods: {
			back() {
				window.history.back();
			},
			go_showtu(panels_id, panels_title) {
				var that = this
				//				this.$router.push('/showtu/' + id)
				if(that.data.user_vip == '0' || that.data.user_vip == '1') {
					if(panels_id <= 2) {
						this.$router.push({
							path: 'showtu',
							query: {
								type: panels_id,
								typeone: panels_title
							}
						})
					} else {
						that.$alert('请充值中级或高级会员查看更多', '提示');
					}
				}else if(that.data.user_vip == '2' || that.data.user_vip == '3'){
					this.$router.push({
							path: 'showtu',
							query: {
								type: panels_id,
								typeone: panels_title
							}
						})
				}

			},
			//			展示面列表
			panelss() {
				var that = this;
				that.$http.post('/panels/panelss', {}).then((res) => {
					if(res.meta.code == 200) {
						that.panelss_list = res.data;
//						if(that.data.user_vip == '0' || that.data.user_vip == '1') {
//							that.panelss_list.length = 2
//							that.panelss_list = res.data;
//							that.$alert('请充值中级或高级会员查看更多','提示');
//						}
					}
				})
			},
			//			个人信息
			userInfo() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					//					discussionid: that.discussionid
				};
				that.$http.post('/user/userInfo', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
						that.panelss();
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
			this.userInfo();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.show-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.show-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		/*border-bottom: 1px solid #eaeaea;*/
	}
	
	.show-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.show-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.show-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.show-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
		padding-left: 1.6rem;
		padding-right: 1.5rem;
	}
	
	.show-between {
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.show-left {
		font-size: 1.5rem;
		color: #000000;
		padding-bottom: 1rem;
		padding-top: 1rem;
	}
	
	.show-jiantou img {
		width: auto;
		height: 1.5rem;
	}
	
	.show-chongzhi {
		font-size: 1.2rem;
		text-align: center;
		padding-top: 4rem;
		color: #ea0b0b;
	}
</style>


